@font-face {
  font-family: KohinoorTelugu-Medium !important;
  src: local('KohinoorTelugu-Medium'),
    url(./fonts/KohinoorTelugu-Medium.ttf) format('ttf');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

body {
  color: rgb(112, 112, 112) !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  color: rgb(112, 112, 112) !important;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.home-title {
  font-size: 28px;
}

.home-desc {
  font-size: 17px;
  text-align: center;
}

.slide-question {
  font-size: 22px;
  text-align: center;
}

.home-desc-cont {
  width: 360px !important;
  text-align: justify;
}

.start-button {
  font-family: KohinoorTelugu-Medium !important;
  margin: 30px;
  font-size: 17px;
  background-color: transparent;
  color: rgb(218, 195, 195) !important;
  border: 2px solid #b8b8b8;
  width: 100px;
  font-family: 'KohinoorTelugu-Medium !important';
  border-radius: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 3px;
}

.next-button {
  font-family: KohinoorTelugu-Medium !important;
  margin: 30px;
  font-size: 17px;
  background-color: transparent;
  color: rgb(218, 195, 195) !important;
  border: 2px solid #b8b8b8;
  width: 100px;
  font-family: 'KohinoorTelugu-Medium !important';
  border-radius: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 3px;
}

button {
  font-family: 'KohinoorTelugu-Medium !important';
}
li > div {
  display: flex;
}

.next-button:hover {
  color: rgb(112, 112, 112) !important;
  background-color: rgb(218, 195, 195) !important;
  cursor: pointer;
}

.start-button:hover {
  color: rgb(112, 112, 112) !important;
  background-color: rgb(218, 195, 195) !important;
  cursor: pointer;
}

.Slide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

textarea {
  border: solid 1px gainsboro;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  caret-color: rgb(218, 195, 195);

  resize: none; /*remove the resize handle on the bottom right*/
}
textarea:focus::placeholder {
  color: transparent;
}

.bot-left-button {
}

.fadein {
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1s; /* Firefox < 16 */
  -ms-animation: fadein 1s; /* Internet Explorer */
  -o-animation: fadein 1s; /* Opera < 12.1 */
  animation: fadein 1s;
}

.slowfadein {
  -webkit-animation: fadein 5s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 5s; /* Firefox < 16 */
  -ms-animation: fadein 5s; /* Internet Explorer */
  -o-animation: fadein 5s; /* Opera < 12.1 */
  animation: fadein 5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fadeout {
  opacity: 0;
  -webkit-animation: fadeout 0.25s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadeout 0.25s; /* Firefox < 16 */
  -ms-animation: fadeout 0.25s; /* Internet Explorer */
  -o-animation: fadeout 0.25s; /* Opera < 12.1 */
  animation: fadeout 0.25s;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* Internet Explorer */
@-ms-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.mapsConsumerUiSceneInternalCoreScene__widget-scene-canvas {
  background-color: white !important;
}
.mapsConsumerUiSceneInternalCoreScene__widget-scene {
  background-color: white !important;
}

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 280px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

#example-searchbox-id {
  height: 30px;
  width: 500px;
  border-radius: 0px;
  border: solid 1px gainsboro;
  font-size: 15px;
}

input:focus {
  outline: none !important;
}

#example-searchbox-id {
  outline: none !important;
  width: 300px;
  margin-top: -20px;
}

textarea {
  width: 600px;
  height: 100px;
}

@media (max-width: 800px) {
  .Slide {
    max-width: 100vw !important;
  }

  div {
  }
  textarea {
    max-width: 80vw !important;
  }
  textarea {
    height: 200px !important;
  }

  .slide-question {
    margin: 10px;
  }
  .Home {
    width: 90vw !important;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .home-desc-cont {
    width: 85vw !important;
  }
}

.box {
  padding: 20px;
  border-radius: 5px;

  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
  -moz-box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 1px 3px;
}
